<template>
<div class="form">
    <div class="d-flex">
      <span class="mx-auto h3 title">Utilisateur</span>
      <span @click="close" class="h2 close ">x</span>
    </div>
<!-- retrieve data -->
<span class="d-none">{{$store.state.users}}{{$store.state.IdEditUser}}</span>
<!-- retrieve data -->
   
  <div class="col-md-12">
    <form action="" @submit.prevent="saveInformation">

      <div class="col-12 d-md-flex d-sm-block">
        <div class="col-md-6 col-sm-12">
            <div>
              <label for="nom">
                  <input type="text"  required="required" id="nom" placeholder="nom" v-model="form.name">
                  <span>Nom</span>
              </label>
                <span>{{ errors?.nom }}</span>
            </div>

          <div>
              <label for="mail">
                  <input type="email" id="mail"   required="required" placeholder="Email" v-model="form.email">
                  <span>Email</span>
              </label>
              <span>{{ errors?.mail }}</span>
          </div>
        </div>
       <!-- <label for="prenom">
            <input type="text" id="prenom" placeholder="prenom" v-model="form.prenom">
            <span>Prenom</span>
        </label>
         <span>{{ errors?.prenom }}</span>
          <label for="dateDenaissance" class="d-block dateWidth">
            <input type="date" id="dateDenaissance"  v-model="form.dateDenaissance">
            <span>Date de naissance</span>
         </label>
         <span>{{ errors?.dateDenaissance }}</span>

         <label for="telephone">
            <input type="tel" id="telephone" placeholder="Telephone" v-model="form.telephone">
            <span>Telephone</span>
         </label>
         <span>{{ errors?.telephone }}</span>--> 
        <!-- <label for="Adresse">
            <input type="text" id="Adresse" placeholder="Adresse" v-model="form.adresse">
            <span>Adresse</span>
         </label>
         <span>{{ errors?.adresse }}</span>-->

        <!--<label for="nomDUtilisateur">
            <input type="text" id="nomDUtilisateur" placeholder="Nom d'Utilasateur" v-model="form.nomDUtilisateur">
            <span>Nom d'Utilisateur</span>
        </label>
        <span>{{ errors?.nomDUtilisateur }}</span>-->

        <div class="col-md-6 col-sm-12">
            <div>
              <label for="motDePasse">
                  <input type="password" id="motDePasse"  required="required" placeholder="Mot de passe" v-model="form.password">
                  <span>Mot de passe</span>
              </label>
          </div>
          <div>
              <label for="confirm">
                  <input type="password" placeholder="Confirmer Mot de passe"  required="required" v-model="form.password_confirmation">
                  <span>Confirmer Mot de passe</span>
              </label>
              <span>{{ errors?.motDePasse }}</span>
          </div>
        </div>
      </div>
      <div class="d-flex justify-content-around">
          <button :disabled="loading" type="submit" class="btn btn-sm btn-danger" >
            <div v-if="loading" class="d-flex justify-content-center mx-2">
						  <span class="">Loading...</span>
                         <div class="spinner-border" role="status">
                          
                         </div>
            </div>
            <span v-if="!loading" class="button__text">{{saveEditBtn}}</span></button>
          <button :disabled="loading" type="reset" v-if="saveEditBtn=='Ajouter'"  class="btn btn-sm btn-primary" >vider</button>
        </div>
</form>
</div>

</div>
</template>

<script>
import api from '../../../../api';
import axios from "axios";
import Swal from 'sweetalert2';
export default {
  props:["modalActive"],
  data() {
    return {
      form: {
        name:"",
     //   prenom:"",
       // dateDenaissance:"",
       // telephone:"",
        email:"",
        loading:false,
       // adresse:"",
       // nomDUtilisateur:"",
        password:"" ,
        password_confirmation:"",
        

      },
      errors: {},
      users:[],
      saveEditBtn:"Ajouter",
    };
  },
  watch:{
  "$store.state.IdEditUser"(a){
    console.log(a)
    if(this.$store.state.IdEditUser==null){
         this.form={};
         this.saveEditBtn="Ajouter"

        }else{
            this.form=this.$store.state.users;
            this.saveEditBtn="Modifier"
        }

  }
 },

  methods: {
    close(){
      this.$emit('close')
    },  
    saveInformation() {
      if (this.form["name", "password", "mail","role_id"]=="") return; 

         
      this.loading=true

       if(this.$store.state.IdEditUser==null){
        if(this.form.password==this.form.password_confirmation)
        {
        api.post(
           "register",
          this.form
        )
        .then((resp) => {
          this.loading=false
          this.users = resp.data;
          Swal.fire({
               icon: 'success',
               title: 'Ajouter',
               text: 'Enregister avec succs',  
              });
              this.$emit('close')
          this.form = {} 
        })
        .catch((err) => {
          this.loading=false
          console.error(err.response.data.errors);
          this.errors = err.response.data.errors;
        });
      }else{
        Swal.fire({
               icon: 'error',
               title: 'mot de passe',
               text: 'Mots de passe ne sont pas les memes',  
              });

      }
       }else{
        api.patch("users/"+this.$store.state.IdEditUser,
          this.form )
        .then((resp) => {
          this.users = resp.data;
          this.$emit('close')
         })
        .catch((err) => {
          console.error(err.response.data.errors);
          this.errors = err.response.data.errors;
        });

       }
 
    }
    
}
}
</script>

<style  scoped src="@/assets/css/form.css">
</style>